body {
	background-color: #fff;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 16px; 
	color: #000;
	height: 100%;
	width: 100%;
	background-image: url(../img/515958.jpg);
	/* background-repeat: no-repeat; */
	background-size:100% 100%;
}

a:link, a:visited {
	color: #4682b4;
}

a:hover {
	color: #4169e1;
}

#map{
	width: 100%;
	height: 600px;
	padding: 10px;
	margin: auto;
}

#main{
	height: 100%;
	width: 100%;
}

#root{
	height: 100%;
	width: 100%;
}
.inActive{
	visibility:hidden
}

.active{
	visibility:none
}

.mybanner{
	background-image: linear-gradient(135deg,#e94e75,#e94e75 56%,#e99c4e 0,#e99c4e 71%,#c2e94e 0,#c2e94e 77%,#4ee94e 0,#4ee94e 82%,#4ee9c2 0,#4ee9c2 90%,#4e9ce9 0,#4e9ce9 97%,#754ee9 0,#754ee9 99%,#e94ee9 0,#e94ee9);;
}

hr {
    display: block;
    unicode-bidi: isolate;
    margin-block-start: 0.5em;
    margin-block-end: 0.5em;
    margin-inline-start: auto;
    margin-inline-end: auto;
    overflow: hidden;
    border-style: inset;
    border-width: 1px;
}
.col {
	flex-basis: 0;
    flex-grow: 1;
    min-width: 0;
    max-width: 100%;
}
